#profile {
  position: relative;
  display: block;
  top: -67px;
}
#profile .container-profile {
  width: 100%;
  display: grid;
  grid-template-columns: 30% 1fr;
}
#profile .container-profile .box-profile img {
  width: 100%;
  filter: var(--filter-profile);
}
#profile .container-profile .content-profile {
  padding: 10em 2em 2em 2em;
  display: block;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  font-family: Modelica;
  font-weight: bold;
}
#profile .container-profile .content-profile .Typewriter {
  color: var(--chocolate-color);
}/*# sourceMappingURL=profile.css.map */